import config from '../config';

function Video() {
    const origin = document.location.origin;
    const src = `${config.youtubeVideoURL}?controls=0&autoplay=1&mute=0&enablejsapi=1&origin=${origin}`;
    console.log(src);
    return <iframe
        width="100%"
        height="100%"
        allow="autoplay"
        src={src}
        title="YouTube video player"
    >
    </iframe>;
}

export default Video;